.quiz {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */ 
    background-color: cadetblue;
    width: 25rem;
    height: 30rem;
    border-radius: 25px;
}

.quiz__start {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; 
    background-color: cadetblue;
    width: 25rem;
    height: 30rem;
    border-radius: 25px;
}

.quiz__start-button {
    width: 10rem;
    height: 3rem;
    font-size: 150%;
    border: solid 2px;
    border-radius: 25px;
    justify-self: center;
    align-self: center;
    background-color: wheat;
    border-color: rgb(26, 72, 73);
    color:  rgb(26, 72, 73);
}


.question {
    width: 90%;
    /* height: 40%; */
    background-color: wheat;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 1rem;
    margin-top: 3rem;
    border-radius: 25px;
}

.question__main {
    width: 100%;
    /* background-color: blue; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 1.3rem;
}

.options {
    width: 90%;
    /* background-color: blue; */
    /* height: 90%; */
    border-radius: 10%;
    margin-top: 0;
    margin: 1.3rem;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
}

.option {
    margin: 0.25rem;
    display: flex;
    align-content: center;
    justify-content: center;
    height: 5rem;
}

.option button {
    width: 20rem;
    border-radius: 2rem;
    background-color: wheat;
}

.quiz__finish {
    padding-top: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.stopwatch {
    padding-left: 0.5rem;
    padding-top: 0.5rem;
}

.startover-button {
    margin-top: 3rem;
    background-color: wheat;
    padding: 0.3rem;
    width: 8rem;
    border-radius: 2rem;
    /* font-weight: ; */
    font-size: 110%;
    border: solid 2px;
    border-color: rgb(26, 72, 73);
    /* color:  rgb(26, 72, 73); */
}

