.mct__shop {
    display: flex;
    /* flex-direction: column; */
    margin-left: 1rem;
}

.mct__info {
    padding-left: 2.5rem;
}

.mct__shop-main {
    display: grid;
    grid-template-columns: 1fr 1fr;
    /* background-color: aliceblue; */
    margin: 1rem;
}

.mct__shop-cart {
    display: table;
    position: sticky;
    top: 2rem;
    width: 100%;
    /* background-color: aliceblue; */
    margin: 1rem;
}

.mct__shop-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0.5rem;
    background-color: aliceblue;
    padding: 0.3rem;
    width: 15rem;
}

.title {
    display: flex;
    height: 5rem;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.cover {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 15rem;
    width: 10rem;
}

.description {
    width: 10rem;
    text-align: center;
    margin: 0.5rem;
    font-weight: bold;
}
.mct__shop-item img {
    max-width: 10rem;
    max-height: 15rem;
}

.mct__shop-cart img {
    width: 1rem;
    height: 1rem;
    background-color: aliceblue;
}

.mct__shop-cart button {
    background-color: aliceblue;
}

.mct__shop-cart table {
    width: 100%;
}

th {
    border-bottom: dashed 1px;
    border-right: 1px dashed;
    height: 2rem;
}

td {
    padding: 0.5rem;
    border-right: 1px dashed;
}


.td_button {
    display: table-cell;
    text-align: center;
    border-right: none;
}

.last {
    border-right: none;
}
.cart_total td {
    border-top: 1px dashed;
    border-right: none;
    height: 2rem;
    font-weight: bold;
}

.total {
    text-align: center;
}

.buttons {
    display: flex;
    justify-content: center;
    align-items: center;
}

.buttons button {
    padding: 0.2rem;
    background-color: cadetblue;
    margin: 0.1rem;
    width: 4.2rem;
    border-radius: 5px;
}

.buttons button:disabled {
    background-color: lightgrey;
}

.qty {
    width: 5rem;
    border: solid cadetblue 3px;
    margin: 0.5rem;
}
