.mct__navbar {
    margin-top: 1rem;
    background: white;
    width: 180px;
    height: 100%;
    align-items: left;
    transition: width 0.3s ease-in-out;
    position: sticky;
}

.mct__navbar_closed {
    margin-top: 1rem;
    background: white;
    width: 40px;
    height: 100%;
    transition: width 0.3s ease-in-out;

}

ul, li {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style-type: none;
}

.mct__navbar-links_container ul {
    display: table;
    width: 180px;
    height: 100%;

    /* background-color: blue; */
}


.mct__navbar-links_container img {
    width: 33px;
    height: 33px;
}

.mct__navbar-links_container button {
    border: none;
    background-color: white;
}

.toggle {
    display: flex;
    align-items: center;
}

.toggle img {
    height: 20px;
    width: 20px;
}

.inside {
    display: table; 
    width: 180px;
    height: 60px;
    /* background-color: aqua; */
}
.inside ul {
    
    width: 180px;
    /* background-color: aqua; */
}
.img {
    width: 40px;
    padding-left: 0.25em;
    vertical-align: middle;
    display: table-cell;
    /* background-color: blueviolet; */
}

.txt {
    width: 140px;
    height: 60px;
    display: table-cell;
    vertical-align: middle;
    /* background-color: brown; */
}

