.mct__calc {
    display: flex;
    flex-direction: column;
    background-color:cadetblue;
    border-radius: 25px;
    margin: 2rem;
    width: 15rem;
    height: 25rem;
    align-items: center;
}

/* вся панель экрана */
.mct__calc-screen {
    background-color: white;
    margin: 1rem;
    border: 2px solid wheat;
    width: 80%;
    height: 15%;
    align-items: right;
}

.mct__display-inp {
    display: flex;
    align-items: right;
    border-bottom: 0.1rem dashed brown;
}

input {
    align-items: right;
    border: none;
}

input:focus {
    outline: none;
}

.mct__display-out {
    border: none;
}

/* панель клавиатуры */
.mct__calc-keys {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: .8rem;
}

.mct__calc-key button {
    height: 40px;
    width: 40px;
    background-color: wheat;
    border: 0;
}