.mct__slideshow-container {
    display: flex;
    flex-direction: column;
    border: 4px cadetblue solid;
    border-radius: 10px;
    margin-left: 2rem;
    padding: 0.3rem;
    /* width: 1000px;
    height: 500px; */
    background-color: cadetblue;
}

.mct___slideshow {
    display: flex;
    
    /* width: 100%; */
}

.mct__slideshow-menu {
    display: flex;
    flex-direction: column;
    width: 1/2rem;
    align-items: center;
    justify-content: center;
    background-color: cadetblue;
}

.mct__slideshow-menu img {
    width: 1.5rem;
    height: 1.5rem;
}

.mct__menu-anim img {
    width: 1.5rem;
    height: 1.5rem;
}

.mct__slideshow-menu button:disabled {
    background-color: brown;
}

.mct__slideshow-main {
    overflow: hidden; 
    /* white-space: wrap; */
    width: 1500px;
    height: 450px;
    align-items: center;
    justify-content: center;
    display: flex;
    /* background-color: white; */
    border: 4px cadetblue solid;
    border-radius: 10px;
}

.mct__slideshow-slide img{
    height: auto;
    width: auto;
    max-width: 1500px;
    max-height: 500px; 
    margin: none;
    padding: none;
}

.mct__slideshow-bottom{
    display: flex;
    justify-content: center;
    align-items: center;
}

.mct__slideshow-top {
    /* width: 2500px;  */
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: 150%;
    
    /* background-color: blueviolet; */
}

.mct__slideshow-top img{
    height: 1.5rem;
    width: 1.5rem;
}

.mct___menu-stats {
    width: 20%;
    align-content: flex-start;
    /* background-color: aqua; */
}

.mct__menu-anim {
    display: flex;
    /* width: 50%; */
    align-content: flex-end;
    padding-left: 1rem;
    /* background-color: blue; */
}

.dot {
    font-size: 50px;
    cursor: pointer;
    margin: 0.3rem;
    color: wheat;
}

.current-dot {
    font-size: 50px;
    cursor: pointer;
    margin: 0.3rem;
    color: rgb(26, 72, 73);
}

button {
    background-color: cadetblue;
    border: none;
}

button:hover {
    cursor: pointer;
}